import { AgentNode, InteractionEdge, SimulationState } from '../types/Agent';

// 
export const MockDataService = {
  getInitialState(): SimulationState {
    const nodes: Record<string, AgentNode> = {};
    const edges: InteractionEdge[] = [];

    // 1.  (Level 1)
    const organs = [
      { id: 'organ-heart', name: '' },
      { id: 'organ-liver', name: '' },
      { id: 'organ-kidney', name: '' },
      { id: 'organ-Intestine', name: '' },
      { id: 'organ-brain', name: 'CNS' },
    ];

    organs.forEach(organ => {
      nodes[organ.id] = {
        id: organ.id,
        name: organ.name,
        level: 'ORGAN',
        status: 'NORMAL',
        metrics: { activity: 0.5, stress: 0.1 },
        childrenIds: [],
        description: `${organ.name}`
      };

      // 2.  (Level 2)
      //  2 
      for (let i = 1; i <= 2; i++) {
        const tissueId = `${organ.id}-tissue-${i}`;
        nodes[tissueId] = {
          id: tissueId,
          name: `${organ.name}-${i}`,
          level: 'TISSUE',
          status: 'NORMAL',
          metrics: { activity: 0.5, stress: 0.1 },
          parentId: organ.id,
          childrenIds: []
        };
        nodes[organ.id].childrenIds?.push(tissueId);

        // 3.  (Level 3)
        for (let j = 1; j <= 2; j++) {
            const cellId = `${tissueId}-cell-${j}`;
            nodes[cellId] = {
                id: cellId,
                name: `${organ.name}-${i}-${j}`,
                level: 'CELL',
                status: 'NORMAL',
                metrics: { activity: 0.5, stress: 0.1 },
                parentId: tissueId,
                childrenIds: []
            };
            nodes[tissueId].childrenIds?.push(cellId);
        }
      }
    });

    // 4.  (Edges)
    edges.push({
      id: 'edge-1',
      sourceId: 'organ-liver',
      targetId: 'organ-heart',
      type: 'SUPPORT',
      intensity: 'MEDIUM',
      description: ''
    });
    
    edges.push({
        id: 'edge-2',
        sourceId: 'organ-kidney',
        targetId: 'organ-liver',
        type: 'DAMAGE', // 
        intensity: 'LOW',
        description: ''
      });

    return {
      nodes,
      edges,
      selectedNodeId: null
    };
  }
};

